(function () {
    'use strict';

    angular.module('app')
        .factory('EnumService', EnumService);

    /* @ngInject */
    function EnumService($http, $q) {
        var cache = {};

        return {
            get: get
        };

        function get(tipo) {
            if (cache[tipo]) {
                var defered = $q.defer();
                var promise = defered.promise;
                defered.resolve(cache[tipo]);
                return promise;
            }

            // FIXME: As sea cachea durante la sesin. Una vez se recarga la pgina la cach se pierde. Se puede tambin cachear la peticin cambiando la URL base (lo que est en /api no se cachea)
            return $http.get('api/public/enum/' + tipo, {cache: true}).then(function (result) {
                cache[tipo] = result.data;
                return result.data;
            });
        }
    }
})();
