(function () {
    'use strict';

    // Botn para la creacin de entidades dentro de otras. Usado para los formularios dentro de ventanas modales.

    // click: Funcin que se va a llamar al pulsar el botn.
    // params: Parmetros que se enviar a la funcin.

    // Ejemplo de uso: FILO1701, traduccion-form.admin.*

    angular
        .module('app')
        .component('buttonCreate', {
            template: '<a ng-click="ctrl.click(ctrl.params)" class="button-create"' +
            ' title="{{\'admin.form.button.create\' | translate}}"><i class="fa fa-plus-square"></i></a>',
            controllerAs: 'ctrl',
            bindings: {
                click: '<',
                params: '<'
            }
        });

})();
