(function () {
    'use strict';

    /**
     * @ngdoc controller
     * @name app.controller:ConfirmModalController
     * @description
     * Controller que, asociado al archivo confirm.modal.html, permite abrir una ventana modal de confirmacin que lanzar una accin.
     *
     * Es obligatorio indicar en el Resolve cada uno de los campos listados a continuacin.
     *
     * @param {Object} item  Elemento del que se muestra informacin
     * @param {Object} params JSON de la forma {title: '', body: '', property: ''}
     * @param {Object} tipo Resource que tiene la funcin que se llamar
     * @param {String} funcion Nombre de la funcin
     * @param {Object} parametros Parameteros que se enviar la a funcin de confirmacin
     * @param {String}
     */
    angular.module('app')
        .controller('ConfirmModalController', Controller);

    /* @ngInject */
    function Controller(item, params, tipo, funcion, parametros, ModalService) {
        var vm = this;

        vm.dismiss = ModalService.dismiss;
        vm.item = item;
        vm.params = params;

        vm.confirm = function () {
            tipo[funcion](parametros).$promise.then(function () {
                ModalService.close(vm.item);
            });
        };
    }
})();
